.comedy{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    padding: 30px;

    &__header{
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: space-between;
        &__text{
            color: aliceblue;
        }
        &__link{
            text-decoration: none;
            font-size: 20px;
            color: yellow;
            &:active{
                color:aliceblue;
                font-weight: 600;
            }
        }
    }
    &__gif{
       margin: 10px;
        width: 45%;
        height: 300px;
        border:1px solid black;
        background-image: url("../../../Assets/img/images.jpg");
        background-size: 100% 100%;
        background-repeat: no-repeat;
    }
    &__box{
        position: relative;
        margin: 10px;
        width: 300px;
        height: 300px;
        border: 1px solid black;
        &__img{
            width: 100%;
            height: 100%;
            display: flex;
        }
        &__title{
            right: 0;
            bottom: 0;
            text-decoration: none;
            color: rgb(13, 34, 224);
            position: absolute;
        }
        &__year{
            top: 0;
            right: 0;
            padding: 5px;
            background-color: white;
            position: absolute;
            color: rgb(216, 21, 21);
            text-decoration: none;
            border-radius: 10px;
        }
    }
}