.Home{
    width: 100%;
   &__bar{
       
       display: flex;
  
   }
   .bar{
       position: relative;
       background-image: url("../../Assets//img/imgsumerki.jpg");
       background-repeat: no-repeat;
       background-size: 100% 100%;
       padding-top: 500px;
       height: 120vh;
       width: 100%;
       display: flex;
       flex-wrap: wrap;
       justify-content: center;
       align-items: flex-end;
       &__text{
           color: white;
           position: absolute;
           top: 0;
           right: 100px;
           width: 300px;
       }
       &__title{
           color: white;
           position: absolute;
           top: 200px;
           width: 200px;
           right: 100px;
       }
       &__pause{
           top: 200px;
           left: 200px;
          margin-left: 0px;
          position: absolute;
       }
       &__box{
           position: relative;
           border: 2px solid blue;
           margin: 10px;
            width: 250px;
             height: 150px;
        
        &__img{
            width: 100%;
            height: 100%;
        }
        &__year{
            position: absolute;
            color: white;
            bottom: 0;
            right: 0;
            background-color: rgb(231, 216, 6);
            padding: 5px;
            border-radius: 10px;
        }
           
       }
   }
    .svzbtn{
        width: 150px;
        height: 40px;
        border-radius: 10px;
        margin: 30px;
        font-size: 20px;
        color: green;
    }
    .VideoPlayer{
        width: 100%;
        height: 600px;
    }
}