.Login__style{
    width: 100%;
    display: flex;
    align-items: center;
    height: 88vh;
    background-image: linear-gradient(to right, yellow , rgb(26, 26, 24));
    &::after{
        margin-left: auto;
        margin-right: 100px;
        content: "";
        display: flex;
        width: 30%;
        height: 100%;
        background-image: url("../../Assets/img/Loginimg2.png");
        background-size: 100% 100% ;
        box-shadow: rgba(254, 254, 255, 0.25) 0px 50px 100px -20px, rgba(255, 236, 236, 0.3) 0px 30px 60px -30px, rgba(255, 255, 255, 0.35) 0px -2px 6px 0px inset;

    }
    &::before{
        display: flex;
        content: "";
        bottom: 0;
       left: 700px;
        z-index: 3;
        width: 30%;
        height: 500px;
        position: absolute;
        background-image: url("../../Assets/img/Loginimg1.png");
        background-size: 100% 100%;
        box-shadow: 0 0 5px -5px rgb(58, 53, 53);
    }
    
}


.Login{
    padding: 20px;
    position: absolute;
    margin-left: 150px;
    width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    &__title{
        color: white;
        margin: 30 0px;
    }
    &__name{
      color: white;
      font-size: 50px;
      margin-bottom: 90px;
    }
    &__email{
        height: 60px;
        width: 100%;
        padding: 0 20px;
        border-radius: 20px;
        font-size: 18px;
        border: none;
    }
    &__pass{
         margin-top:30px ;
        height: 60px;
        width: 100%;
        padding: 0 20px;
        border-radius: 20px;
        font-size: 18px;
        border: none;
    }
    &__btn{
        margin-top: 30px;
        width: 110px;
        height: 55px;
        border-radius: 20px;
        background-color: rgba(254, 213, 48, 1);
        border: none;
        box-shadow: 0 0 5px rgb(190, 15, 15);
    }

}